.education {
  display: grid;
  grid-template-columns: 10vw 1fr 1fr 10vw;
  grid-template-rows: 50px 100px 100px 60px;
  justify-content: center;
  margin-bottom: 50px;
  padding-bottom: 50px;
  border-bottom: 2px solid rgb(134, 9, 34);
}

#pschool-name,
#pschool-title,
#pschool-start,
#pschool-end {
  text-align: center;
  font-family: 'Raleway';
  font-size: 30px;
}

#school-name,
#pschool-name {
  grid-column: 2;
  grid-row: 2;
}

#school-title,
#pschool-title {
  grid-column: 3;
  grid-row: 2;
}

#school-start,
#pschool-start {
  grid-column: 2;
  grid-row: 3;
}

#school-end,
#pschool-end {
  grid-column: 3;
  grid-row: 3;
}

#delete-button-education {
  grid-column: 4;
  grid-row: 1;
  width: 40px;
  height: 40px;
  background-color: crimson;
  border: 2px solid rgb(97, 0, 0);
  border-radius: 50px;
  color: white;
  font-size: 20px;
  transition: all 0.15s ease;
}

#delete-button-education:hover {
  background-color: white;
  color: crimson;
}

#edit-submit-button-education {
  grid-column: 3/5;
  grid-row: 4;
}
